import React from "react";
import { Helmet } from "react-helmet";

import "./anyPage.scss";
import underConstructionImage from "../../assets/images/under-construction.png";

const AnyPage = () => {
	return (
		<div className="anypage">
			<Helmet>
				<title>Any page - Smart Technics</title>
				<meta
					name="description"
					content={
						"If you are on this page, it means you wanted to visit a page that has not been developed yet"
					}
				/>
			</Helmet>
			<div className="container">
				<div className="anypage__inner">
					<div className="anypage__image">
						<img src={underConstructionImage} alt="presentation image" aria-hidden="true" />
					</div>
					<h2 className="anypage__title">This page has not been developed yet</h2>
				</div>
			</div>
		</div>
	);
};

export default AnyPage;
